.green {
  color: #047857;
  text-align: start;
  padding: 0.5rem 1rem;
  font-weight: bold;
}
.yellow {
  color: #f59e0b;
  padding: 0.5rem 1rem;
  text-align: start;
  font-weight: bold;
}
.red {
  color: #ef4444;
  padding: 0.5rem 1rem;
  text-align: start;
  font-weight: bold;
}
#rating {
  font-weight: bold;
  padding: 0;
}

/* signup */
.container {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.title {
  color: #818cf8;
  font-family: "Poppins", sans-serif;
}
.imagediv {
  border: none;
  overflow: hidden;
  max-height: 10%;
  position: relative;
  opacity: 0.8;
}
.main-modal {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(139, 150, 143, 0.5);
}

/* .main-modal img {
  max-height: 80%;
  max-width: 60%;
  display: block;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.5);
} */
.sub-modal {
  
  /* height: 60%; */
  position: absolute;
  /* background-color: gray; */
  max-width: 500px;
  margin: auto;
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.sub-modal img {
  border-radius: 1rem 1rem 0 0;
  height: 250px;
  object-fit: fill;
  width: 100%;
  /* object-fit: contain; */
}
.over-para {
  text-align: start;
  padding: 0.5rem 1rem;
  margin: 0 auto;
  /* background-color: black; */
  color: white;
}
.modal-overview {
  /* display: flex; */
  background-color: gray;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  max-height: 10%;
  max-width: 100%;
}

.modal-overview h3 {
  margin: 8px 16px;
  text-align: start;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

